}

#header nav ul li a {
    color: white;
    border: 1px solid #0b2491;
    background: linear-gradient(#2071bd, #10659e);
    border-radius: 2px;
    box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.753), 0px 3px 7px rgba(0, 0, 0, 0.842);
    background-color: #2090bd;
    padding: 10px 12px;
    margin-top: 6px;
    line-height: 16px;
    font-size: 16px;
    font-weight: bold;
    display: inline-block;
    text-align: center
}

#header nav ul li a:hover {
    background: linear-gradient(#2069bd, #105e9e);
    background-color: #2083bd;
    color: #ffffff;
    font-weight: bold;
    border: 1px solid #030303;
    box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.2), 0px 1px 0px transparent
}

#header nav ul li.fork {
    float: left;
    margin-left: 20px;
    margin-right: 40px;
    
}

#header nav ul li.downloads {
    float: right;
    margin-left: 6px
